import React, { useState } from 'react';

import {
  Box,
  Tabs, Tab,
} from '@mui/material';
import {
  GridView as GridViewIcon,
  ViewCompact as ViewCompactIcon,
  ViewList as ViewListIcon,
} from '@mui/icons-material';

import AppGridView from './AppGridView';
import AppViewCompact from './AppViewCompact';
import AppViewList from './AppViewList';


function SelectAppView(props) {
  const [tabValue, setTabValue] = useState('GridView');

  return (
    <Box sx={{
      mt: 4,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      width:'100%',
    }}>
      <Tabs
        value={tabValue}
        onChange={(event, newTabValue) => setTabValue(newTabValue)}
      >
        <Tab value='GridView' icon={<GridViewIcon />} />
        <Tab value='ViewCompact' icon={<ViewCompactIcon />} />
        <Tab value='ViewList' icon={<ViewListIcon />} />
      </Tabs>

      {tabValue === 'GridView' &&
        <AppGridView
          private={props.private}
        />
      }

      {tabValue === 'ViewCompact' &&
        <AppViewCompact
          private={props.private}
        />
      }

      {tabValue === 'ViewList' &&
        <AppViewList
          private={props.private}
        />
      }

    </Box>
  )

}

export default SelectAppView;