import React from 'react';

import {
  Container, Grid, Card, CardActionArea, CardContent, Typography,
} from '@mui/material';
import {
  grey,
} from '@mui/material/colors';
import {
  maitakeApps,
} from './Config'


function AppGridView(props) {
  const filteredApps = maitakeApps.filter((app) => props.private || app.public);

  return (
    <Container sx={{ py: 2 }} maxWidth='md'>
      <Grid container spacing={4}>
        {filteredApps.map((app) => (
          <Grid item key={app.title} xs={12} sm={6} md={4}>
            <Card
              sx={{
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <CardActionArea component='a' href={app.url}>
                <CardContent
                  sx={{
                    flexGrow: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    color: app.color[500],
                    backgroundColor: grey[100],
                  }}>
                    {React.cloneElement(app.icon, { style: { fontSize: 64 } })}
                </CardContent>

                <CardContent
                  sx={{
                    flexGrow: 1,
                    backgroundColor: app.color[100],
                  }}>
                  <Typography gutterBottom variant='h5' component='h2'>
                    {app.title}
                  </Typography>
                  <Typography variant='subtitle1' paragraph>
                    {app.description}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  )

}

export default AppGridView;