import React from 'react';

import {
  Container, Grid,
  IconButton,
} from '@mui/material';

import {
  maitakeApps,
} from './Config'


function AppViewCompact(props) {
  const filteredApps = maitakeApps.filter((app) => props.private || app.public);

  return (
    <Container sx={{ py: 2 }} maxWidth='md'>
      <Grid container
        justifyContent='center'
        alignItems='center'
        spacing={2}
      >
        {filteredApps.map((app) => (
          <Grid item
            key={app.title}
            xs={3}
            justifyContent='center'
            alignItems='center'
            style={{ textAlign: 'center' }}
          >
            <IconButton
              sx={{ color: app.color[500] }}
              href={app.url}
            >
              {React.cloneElement(app.icon, { style: { fontSize: 64 } })}
            </IconButton>
          </Grid>
        ))}
      </Grid>
    </Container>
  )

}

export default AppViewCompact;