import React from 'react';

import {
  Container,
  List, ListItem, ListItemButton, ListItemIcon, ListItemText,
} from '@mui/material';

import {
  maitakeApps,
} from './Config'


function AppViewList(props) {
  const filteredApps = maitakeApps.filter((app) => props.private || app.public);

  return (
    <Container sx={{ py: 2 }}>
      <List>
        {filteredApps.map((app) => (
          <ListItem disablePadding
            key={app.title}
            sx={{ bgcolor: app.color[100] }}          >
            <ListItemButton
              href={app.url}
            >
              <ListItemIcon
                sx={{ color: app.color[500] }}
              >
                {app.icon}
              </ListItemIcon>
              <ListItemText primary={app.title} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Container>
  )

}

export default AppViewList;