import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from './AuthContext';
import {
  Box, Button,
} from '@mui/material';
import {
  Logout as LogoutIcon,
} from '@mui/icons-material';

import SelectAppView from './children/SelectAppView';




function Main() {
  const [canAuth, setCanAuth] = useState(true);

  const navigation = useNavigate();

  const { isAuth, isRunning, setIsRunning, authSignOut, authVerifyJwt } = useAuth();



  // 
  useEffect(() => {
    (async () => {
      setIsRunning(true);

      if (canAuth) {
        setCanAuth(false);
        // JWT
        await authVerifyJwt();
      }
      setIsRunning(false);
    })()
  }, []);

  // isAuth
  useEffect(() => {
    // JWTSignUpAndSignIn
    if (!isAuth) {
      navigation('/');
    }
  }, [isAuth]);



  // 
  const clickSignOut = async () => {
    setIsRunning(true);
    //SignUpAndSignIn
    await authSignOut();
    navigation('/');
    setIsRunning(false);
  }


  return (
    <Box sx={{
      mt: 12,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    }}>

      <img src={`${process.env.PUBLIC_URL}/logo192.png`} alt='' />

      <Button
        onClick={clickSignOut}
        variant='outlined'
        disabled={isRunning}
        startIcon={<LogoutIcon />}>
        
      </Button>

      <SelectAppView
        private={true}
      />

    </Box >
  );
}

export default Main;
