import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { useAuth } from './AuthContext';
import {
  Container, Box, Tooltip,
  Dialog, DialogTitle, DialogContent,
  Avatar, Typography, TextField, InputAdornment,
  FormControl, FormHelperText, RadioGroup, FormControlLabel, Radio,
  Grid,
  Button, IconButton,
} from '@mui/material';
import {
  Cookie as CookieIcon,
  Person as PersonIcon,
  Key as KeyIcon,
  Login as LoginIcon,
} from '@mui/icons-material';

import SelectAppView from './children/SelectAppView'


function SignUpAndSignIn() {
  const [isAcceptAllCookie, setIsAcceptAllCookie] = useState(false);
  const [canAuth, setCanAuth] = useState(true);

  const navigation = useNavigate();

  const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm({ mode: 'onChange', criteriaMode: 'all' });

  const { isAuth, isRunning, setIsRunning, cookies, setCookie, authSignUpAndSignIn, authVerifyJwt } = useAuth();



  // 
  useEffect(() => {
    (async () => {
      setIsRunning(true);
      // CookieIS_ACCEPT_ALL_COOKIEisAcceptAllCookie
      if (!(cookies.IS_ACCEPT_ALL_COOKIE === true)) {
        setIsRunning(false);
        return;
      }
      setIsAcceptAllCookie(true);

      if (canAuth) {
        setCanAuth(false);
        // jwt
        await authVerifyJwt();
      }
      setIsRunning(false);
    })()
  }, []);

  // isAuth,isAcceptAllCookie
  useEffect(() => {
    // Main
    if (isAuth) {
      navigation('/Main');
    }
  }, [isAuth, isAcceptAllCookie]);



  // Cookie
  const clickAcceptAllCookie = () => {
    setIsRunning(true);
    // 365
    const cookieMaxAge = 3600 * 24 * 365;
    // CookieisAcceptAllCookieIS_ACCEPT_ALL_COOKIEtrue
    setCookie('IS_ACCEPT_ALL_COOKIE', true, { path: '/', maxAge: cookieMaxAge, secure: true, sameSite: 'strict' });
    setIsAcceptAllCookie(true);
    setIsRunning(false);
  }



  // 
  const onSubmit = async (formData) => {
    setIsRunning(true);
    // CookieIS_ACCEPT_ALL_COOKIEfalse
    if (!cookies.IS_ACCEPT_ALL_COOKIE) {
      // isAcceptAllCookiefalseonSubmit
      setIsAcceptAllCookie(false);
      setIsRunning(false);
      return;
    }

    //Main
    await authSignUpAndSignIn(formData);
    if (isAuth) {
      navigation('/Main');
    }
    setIsRunning(false);
  }

  const actionItems = [
    { label: '', value: 'signUp', color: 'secondary', tooltip: '' },
    { label: '', value: 'signIn', color: 'primary', tooltip: '' }
  ];



  return (
    <Box>
      <Container component='main' maxWidth='xs'>
        <Box
          sx={{
            marginTop: 16,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Dialog
            open={!isAcceptAllCookie}
            PaperProps={{
              style: {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }
            }}>
            <DialogTitle>Cookie</DialogTitle>
            <DialogContent>
              <Tooltip title='Cookie'>
                <Button
                  variant='outlined'
                  onClick={clickAcceptAllCookie}>
                  <CookieIcon />
                  Cookie
                </Button>
              </Tooltip>
            </DialogContent>
          </Dialog>

          <Avatar
            src={`${process.env.PUBLIC_URL}/logo180.png`}
            alt=''
            sx={{ width: 64, height: 64 }}>
          </Avatar>

          <Typography
            component='h1'
            variant='h5'
            sx={{ mt: 4 }}>
            
          </Typography>

          <Box sx={{ mt: 2 }}>
            <TextField
              label=''
              type='text'
              placeholder='112'
              required
              autoFocus
              {...register('userName', {
                required: '',
                pattern: { value: /^[A-Za-z]+$/, message: '' },
                minLength: { value: 1, message: '1' },
                maxLength: { value: 12, message: '12' }
              })}
              error={!!errors.userName}
              helperText={errors.userName && errors.userName.message}
              margin='normal'
              fullWidth
              disabled={isRunning}
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <PersonIcon />
                  </InputAdornment>
                ),
                style: { fontSize: 16 }
              }}
            />

            <TextField
              label=''
              type='password'
              placeholder='12'
              required
              {...register('userPassword', {
                required: '',
                pattern: { value: /^[A-Za-z]+$/, message: '' },
                minLength: { value: 12, message: '12' },
                maxLength: { value: 12, message: '12' }
              })}
              error={!!errors.userPassword}
              helperText={errors.userPassword && errors.userPassword.message}
              margin='normal'
              fullWidth
              style={{ width: '100%' }}
              disabled={isRunning}
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    <KeyIcon />
                  </InputAdornment>
                ),
                style: { fontSize: 16 }
              }}
            />

            <Grid container sx={{
              mt: 2,
              alignItems: 'center',
            }}>
              <Grid item xs={10}>
                <FormControl error={!!errors.action}>
                  <RadioGroup row defaultValue='signIn'>
                    {actionItems.map((item) => (
                      <Tooltip key={item.value} title={item.tooltip}>
                        <span>
                          <FormControlLabel
                            value={item.value}
                            label={item.label}
                            {...register('action', {
                              required: '',
                            })}
                            control={
                              <Radio
                                color={item.color}
                                disabled={isRunning}
                              />
                            }
                          />
                        </span>
                      </Tooltip>
                    ))}
                  </RadioGroup>
                  <FormHelperText>{errors.action && errors.action.message}</FormHelperText>
                </FormControl>
              </Grid>

              <Grid item xs={2}>
                <Tooltip title=''>
                  <span>
                    <IconButton
                      onClick={handleSubmit(onSubmit)}
                      aria-label=''
                      color='primary'
                      disabled={!isAcceptAllCookie || !isDirty || !isValid || isRunning}>
                      <LoginIcon fontSize='large' />
                    </IconButton>
                  </span>
                </Tooltip>
              </Grid>
            </Grid>
          </Box>
        </Box>

      </Container>

      <SelectAppView
        private={false}
      />
      
    </Box>
  );
}

export default SignUpAndSignIn;
